import React, { useEffect, useState } from 'react';
import {FormEdit} from 'react-formio';
import axios from 'axios';
import { CCard, CCardBody, CCol, CSwitch } from '@coreui/react';
import { useSelector } from 'react-redux';

export default function EditForm(props) {
    const form_state = props.location.state.form
    const [form, setForm] = useState(form_state.form);
    const [users, setUsers] = useState([])
    const components = useSelector(state=> state.permissionReducer);

    const onSaveForm = (form)=>{
        if(form.title){
        axios.put(`/form/edit/${form_state._id}`,form).then(res=>{
            props.history.push(`/form/display/${form_state._id}`)

        }).catch(err=>{
        alert("Something Wrong")
        })}else{
            alert("you should add title")

        }
    }

  return (
        <div>
            <h1>Edit Form</h1>
            <hr/>
            
            
            <FormEdit 
                saveText="Edit Form"
                form ={form}
                saveForm = {onSaveForm}    
                options = {{
                    builder: {
                        layout: false,
                        data: false,
                        basic: false,
                        advanced: false,
                        premium: false,
                        customBasic: {
                            title: 'Elements',
                            default: true,
                            weight: 0,
                            components: {
                              textfield:true,
                              datetime:true,
                              day:true,
                              time:true,
                              textarea: true,
                              uploadImage: {
                                title: 'Upload Image',
                                icon: 'image',
                                schema: {
                                  storage: "base64",
                                  label: 'Image',
                                  image:true,
                                  type: 'file',
                                  key: 'image',
                                  input: true
                                },
   
                            },
                            uploadImages: {
                                title: 'Upload Images',
                                icon: 'image',
                                schema: {
                                  storage: "base64",
                                  label: 'Images',
                                  image:true,
                                  type: 'file',
                                  key: 'images',
                                  input: true,
                                  multiple: true,
                                  persistent: false,
                                
                                },
   
                            }
                        }
                    }
                 }
                }}
            />
             <div>
                <h3>Permissions</h3>
                <CCol xs={12}>
                    <CCard className="mb-4">
                    <CCardBody>
                        <p className="text-medium-emphasis small">
                            This Table to handle permssions on form
                        </p>
                        <table className="table">
                        <thead>
                            <tr>
                                <th></th>
                              
                                {users.map(user=><th>{user.username}</th>)}
                            </tr>
                        </thead>
                        <tbody>
                                {components.map(component=>{return <>
                                        <tr>
                                            <td>{component.label}</td>
                                            {users.map(user=><td>
                                                <CSwitch className={'mx-1'} 
                                                        variant={'3d'} 
                                                        color={'primary'} 
                                                        onChange={(e)=>
                                                            {if (e.target.checked){
                                                               component.users.add(user.username)
                                                               console.log(component.users)
                                                            }else{
                                                                component.users.delete(user.username)
                                                                console.log(component.users)
                                                            }}
                                                        }  />
                                            </td>)}
                                        </tr>
                                </>})}
                            
                        </tbody>
                        </table>
           
                        </CCardBody>
                        </CCard>
                </CCol>
            </div>

        </div>
    );
  
}